import morse from "./alphabet.json";

export const translate = (morseCode) => {
  const parsedLetters = morseCode.reduce((acc, curr) => {
    console.log(acc, curr);
    if (curr === " ") {
      return [...acc];
    } else if (curr === "   " || curr === "       ") {
      return [...acc, curr];
    } else {
      let lastChar = acc[acc.length - 1] ? acc[acc.length - 1] : "";
      let newValue =
        lastChar === "   " || lastChar === "       "
          ? [lastChar, curr]
          : [lastChar + curr];

      return [...acc.slice(0, acc.length - 1), ...newValue];
    }
  }, []);

  let translation = parsedLetters.map((str) => {
    return str in morse.morse ? morse.morse[str] : str !== "   " ? str : "";
  });
  return { parsedLetters: parsedLetters, translation: translation };
};
