import React from "react";

export class MorseSettings extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      keybindings: {
        backspace: "Clear & Reset",
        esc: "Start/Stop session",
        "any other key": "Clicker",
      },
    };
  }

  render() {
    return (
      <div>
        <table style={{ width: "30%" }}>
          <tbody>
            {Object.keys(this.state.keybindings).map((key, idx) => {
              return (
                <tr key={idx}>
                  <td style={{ textAlign: "right" }}>{key}: </td>
                  <td style={{ textAlign: "center" }}>
                    {this.state.keybindings[key]}
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
        <div>
          {this.props.settings.sections.map((section, idx) => (
            <div class="tooltip">
              <span class="tooltiptext">{section.hotkey}</span>
              <button>{section.label}</button>
            </div>
          ))}
        </div>
        <input
          type="range"
          min="0.05"
          max="0.5"
          value={this.props.settings.dotTime}
          className="slider"
          id="myRange"
          step="0.01"
          onChange={(e) => {
            this.props.changeSettings({ dotTime: e.target.value });
          }}
        />
        <div>{this.props.settings.displayMode}</div>
      </div>
    );
  }
}
