import React from "react";
import morse from "./alphabet.json";
import { MorseSettings } from "../settings";
import { MorseSchool } from "../morseSchool";
import { Device } from "./device";

export class Morse extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      settings: {
        dotTime: 0.5,
        sections: [
          { label: "Freestyle", hotkey: "ctrl + f" },
          { label: "Training", hotkey: "ctrl + t" },
          { label: "Decryption", hotkey: "ctrl + d" },
        ],
        displayMode: "Freestyle",
      },
    };
  }

  changeSettings = (obj) => {
    this.setState({ ...obj });
  };

  render() {
    const { displayMode } = this.state.settings;
    const readingClass = this.state.reading
      ? "reading-class"
      : "not-reading-class";
    return (
      <div>
        <MorseSettings
          settings={this.state.settings}
          changeSettings={this.changeSettings}
        />
        {displayMode === "Freestyle" ? (
          <div>
            <Device
              dotTime={this.state.settings.dotTime}
              setCode={this.changeSettings}
              reading={this.state.reading}
            />
            <div id="morse-display" className={readingClass}>
              {this.state.translation}
              <br />
              {readingClass}
            </div>
          </div>
        ) : displayMode === "Training" ? (
          <MorseSchool dotTime={this.state.settings.dotTime} />
        ) : null}
        <table style={{ width: "100%" }}>
          <tbody
            style={{ width: "100%", float: "left" }}
            className="centered has-border"
          >
            {Object.keys(morse.remember).map((key, idx) => {
              return (
                <tr key={idx}>
                  <td style={{ textAlign: "right" }}>{key}: </td>
                  <td style={{ textAlign: "center" }}>{morse.remember[key]}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    );
  }
}
