import React from "react";
import morse from "./alphabet.json";
import { translate } from "./utils";

export class Device extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tapStart: null,
      tapLastEnd: null,
      key: null,
      tapN: 0,
      tapDurations: [],
      morseCode: [],
      lastDuration: null,
      lastSymbol: null,
      parsedLetters: [],
      averageTap: 0,
      reading: false,

      values: Object.values(morse.morse),
      currentChar: Object.values(morse.morse)[0],
      currentSubmission: null,
    };
  }

  componentDidMount = (props) => {
    window.onblur = (event) => {
      this.stopListening();
    };
    document.body.addEventListener("keydown", (event) => {
      if (event.key === "Backspace") {
        this.reset();
      } else if (event.key === "Escape") {
        this.onOff();
      } else if (this.state.key !== event.key && this.state.reading) {
        this.recordKeyDown(event);
      }
    });
    document.body.addEventListener("keyup", (event) => {
      if (event.key !== "Escape") {
        this.recordKeyUp(event);
      }
    });
  };

  onOff = () => {
    let newMorseCode = [...this.state.morseCode];
    if (this.props.setCode)
      this.props.setCode({ reading: !this.props.reading });
    this.setState({ reading: !this.state.reading, morseCode: newMorseCode });
  };

  stopListening = () => {
    let newMorseCode = [...this.state.morseCode];
    if (this.props.setCode) this.props.setCode({ reading: false });

    this.setState({
      reading: false,
      tapStart: null,
      tapLastEnd: null,
      morseCode: newMorseCode,
    });
  };

  recordKeyDown = (event) => {
    let newCode = [];
    if (this.state.tapLastEnd) {
      let noKeyTime = Math.round(
        ((event.timeStamp - this.state.tapLastEnd) / 1000) * this.props.dotTime
      );
      newCode =
        noKeyTime <= this.props.dotTime * 1
          ? [" "]
          : noKeyTime <= this.props.dotTime * 3
          ? ["   "]
          : ["       "];
    }
    let newMorseCode = [...this.state.morseCode, ...newCode];
    this.setState({
      key: event.key,
      morseCode: newMorseCode,
      tapLastEnd: null,
      tapStart: event.timeStamp,
    });
  };

  recordKeyUp = (event) => {
    if (this.state.reading) {
      const newDuration = (event.timeStamp - this.state.tapStart) / 1000;
      const tapDurations = [...this.state.tapDurations, newDuration];
      let avg = tapDurations.reduce((a, b) => a + b) / tapDurations.length;
      const newCode =
        newDuration <= this.props.dotTime
          ? "*"
          : newDuration <= this.props.dotTime * 3.5
          ? "-"
          : "";
      const newMorseCode = [...this.state.morseCode, newCode];

      const { parsedLetters, translation } = translate(newMorseCode);
      if (this.props.setCode) this.props.setCode({ translation: translation });

      this.setState({
        key: null,
        tapStart: null,
        tapLastEnd: event.timeStamp,
        tapDurations: tapDurations,
        lastDuration: newDuration,
        lastSymbol: newCode,
        morseCode: newMorseCode,
        parsedLetters: parsedLetters,
        translation: translation,
        averageTap: avg,
      });
    }
  };

  changeSettings = (obj) => {
    this.setState({ ...obj });
  };

  reset = (event) => {
    if (this.props.setCode) this.props.setCode({ reading: false });

    this.setState({
      tapStart: null,
      tapLastEnd: null,
      key: null,
      tapN: 0,
      tapDurations: [],
      morseCode: [],
      lastDuration: null,
      lastSymbol: null,
      averageTap: 0,
      reading: false,
    });
  };

  render() {
    let { tapDurations, translation, parsedLetters } = this.state;

    return (
      <div>
        {parsedLetters.join("")}
        <div>
          <span className="stat-item">
            Selected Dot Time: {this.props.dotTime}
          </span>
          <span className="stat-item">
            Selected Dash Time: {this.props.dotTime * 3}
          </span>
          <span className="stat-item">
            Last Duration: {this.state.lastDuration}
          </span>
          <span className="stat-item">
            Last Symbol: {this.state.lastSymbol}
          </span>
          <span className="stat-item">
            Average:
            {tapDurations.reduce((a, b) => a + b, 0) /
              tapDurations.length /
              1000}
          </span>
        </div>
        <button onClick={this.reset}>Clear</button>
      </div>
    );
  }
}
