import React from "react";
import morse from "../morse/alphabet.json";
import { Device } from "../morse/device";

export class MorseSchool extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      values: Object.values(morse.morse),
      currentChar: Object.values(morse.morse)[0],
      currentSubmission: null,
      translation: null,
      reading: false,
    };
  }

  setCode = (codeObj) => {
    this.setState(codeObj);
  };

  render() {
    const readingClass = this.state.reading
      ? "reading-class"
      : "not-reading-class";
    return (
      <div>
        Morse School
        <div>
          <h4>Character:</h4>
          <div>{this.state.currentChar}</div>
          <h4>Input:</h4>
          <div>{this.state.currentSubmission}</div>
          <Device
            dotTime={this.props.dotTime}
            setCode={this.setCode}
            reading={this.state.reading}
          />
          <div>
            School Translation
            <br />
            <div id="morse-display" class={readingClass}>
              {this.state.translation}
              <br />
              {readingClass}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
